*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
  background-color: #f0f0f0;
}

button,
label {
  cursor: pointer;
}

input {
  font: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.timer {
  display: flex;
  margin-top: 20px;
}

.timer > .field {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.timer > .field:not(:last-child) {
  margin-right: 20px;
}

.timer .value {
  color: #ffffff;
  background-color: #3c3c3c;

  margin-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
  font-family: 'Roboto Mono', monospace;
  font-size: 80px;
}

.timer .label {
  color: #a0a0a0;

  font-family: 'Roboto Mono', monospace;
  font-size: 14px;
  text-transform: uppercase;
}
